import React from 'react'
//import PRODUCTS from './products.js'
import {Container, Row, Col, Button} from 'react-bootstrap'
import { useRouteMatch, useHistory } from 'react-router-dom'
import AppContext from './context'


export default function DetailsPage(props) {
    const context = React.useContext(AppContext)
    const Prod_AR = Object.values(context.products)
    const match = useRouteMatch('/details/:catID')
    const useRM = parseInt(match.params.catID)
    const obj = Prod_AR.find(match => match.id === useRM)
    const [imgIdx, setImgIdx] = React.useState(1)

    let history = useHistory();
      
    function handleClick() {
        history.push("/cart") 
    }

    if (obj == null) {
        return (
            <Container fluid className="p-0">
                <Row noGutters style={{ padding: "6rem 0"}}>
                    <Col style={{padding: "1rem"}}>
                        <center>
                            <h1>
                                404 - Not Found 
                            </h1>                         
                        </center>
                        <br></br><br></br><br></br>
                        <img alt="404" className="center-2" src={`${process.env.PUBLIC_URL}/gallery/404.png`}></img>
                    </Col>
                </Row>
            </Container>
        )
    }

    const img = [1, 2, 3, 4]

    return (
        <Container fluid className="p-0">
            <Row noGutters style={{ padding: "1rem 0"}}>
                <Col style={{padding: "1rem"}}>
                    <>                                           
                        <h1>{obj.name}</h1>                        
                        <h2>${obj.price}</h2>                                           
                        <p style={{fontSize: "1.2rem"}}>{obj.description}</p>
                        <Button onClick={e => {context.addToCart(obj.id); handleClick()}} className='mt-5 ml-1' variant="warning">Add To Cart</Button>                    
                    </>
                </Col>
                <Col xs={5}>
                    <img style={{margin: "2rem"}} className="float-right imgsize" alt={`${obj.name}`} src={`${process.env.PUBLIC_URL}/gallery/products/${obj.filename}-${imgIdx}.png`}></img> 
                    <div className="float-right" style={{paddingRight: "6rem"}}>
                        {img.map((p, index) => {                            
                        return (
                            <button key={p} style={{ margin: ".4rem", backgroundImage: `url(${process.env.PUBLIC_URL}/gallery/products/${obj.filename}-${p}.png)`, backgroundSize: "cover"}} className="smallimg" onMouseEnter={() => setImgIdx(p)}></button>                                
                        )
                        })}   
                    </div>
                </Col>
            </Row>
        </Container>
    )
    
}