import React from 'react'
import {Row} from 'react-bootstrap'
//import PRODUCTS from './products.js'
import { useRouteMatch } from 'react-router-dom';
import CardCtg from './card';
import AppContext from './context'


export default function ProductCardCat(props) {
    const context = React.useContext(AppContext)
    let Prod_AR = Object.values(context.products)

    //let Prod_AR = Object.values(PRODUCTS)
    const match = useRouteMatch('/category/:catName')

    if(match) {
        Prod_AR = Prod_AR.filter(p => p.category.title === match.params.catName)
    }

    return (
        <Row noGutters style={{ padding: "3rem 0"}}>
            {Prod_AR.map(p => {
                return (
                    <CardCtg p={p} key={p.id}/>
                )
            })} 
        </Row>
    )        
}