import React from 'react'
import {Link} from "react-router-dom"
import {Container, Row, Col} from 'react-bootstrap'
import AppContext from './context'
import CartRow from './cart-row'

export default function Cart(props) {    
    const context = React.useContext(AppContext)
    const Prod_AR = Object.values(context.products)
    let cart = context.cart
    let aCart = Object.keys(cart)

    //change keys from string to int
    let aiCart = []
    for (let i in aCart) {
        let iID = parseInt(aCart[i])
        aiCart.push(iID)
    }

    //find the product object and put it in a dictionary
    let aProds = {}
    for (let k in aiCart) {
        let obj = Prod_AR.find(p => p.id === aiCart[k])
        aProds[aiCart[k]] = obj
    }

    //get the values of the dictionary
    let oProds = Object.values(aProds)

    let tot = 0

    //total price
    for (let s in oProds) {
        tot += parseInt(oProds[s].price)*cart[oProds[s].id]
    }

    let total = tot.toFixed(2)
    

    return (
        <Container>
            <Row noGutters className="mb-4">
                <Col className="mt-4 ml-3">
                    <h1>Shopping Cart</h1>
                </Col>
            </Row>
            <Row noGutters className="ml-2">
                <Col md="3">
                    <Container>
                        <p style={{fontSize: "1.2rem", paddingBottom: "1rem"}}>Product</p>
                    </Container>
                </Col>
                <Col md="2">
                    <Container>
                        <p style={{fontSize: "1.2rem", paddingBottom: "1rem"}}>Quantity</p>
                    </Container>
                </Col>
                <Col md="2">
                    <Container>
                        <p style={{fontSize: "1.2rem", paddingBottom: "1rem"}}>Price</p>
                    </Container>
                </Col>
                <Col md="2">
                    <Container>
                        <p style={{fontSize: "1.2rem", paddingBottom: "1rem"}}>Extended</p>
                    </Container>
                </Col>
                <Col md="3">
                    <Container>
                        <p style={{fontSize: "1.2rem", paddingBottom: "1rem"}}>Remove</p>
                    </Container>
                </Col>
            </Row>
            <Row style={{paddingBottom: '2rem'}}>
                {oProds.map(p => {
                    return (
                        <CartRow p={p} q={cart[p.id]} key={p.id}/>
                    )
                })}
            </Row>
            <Row>
                <Col md="8" className="ml-4">
                    <strong><h3>Total</h3></strong>
                </Col>
                <Col md="3">
                    <strong><h3>${total}</h3></strong>
                </Col>
            </Row>
            <Row className="mt-4 ml-1">
                <Col md='2'>                    
                </Col>
                <Col md='2'>                    
                </Col>
                <Col md='4'>                    
                </Col>
                <Col md='4'>
                    <Link className="btn btn-success mt-3" to={`/checkout`}>Go to Checkout</Link>
                </Col>
            </Row>
        </Container>
    )
}