import React from 'react'
import axios from 'axios'
import AppContext from './context'
import App from './App'
import produce from 'immer'

/** The context provider for our app */
export default class AppProvider extends React.Component {

    constructor(props) {
        super(props)
        this.actions = {
            addToCart: this.addToCart,
            removeFromCart: this.removeFromCart,
            getCartTotal: this.getCartTotal,
            emptyCart: this.emptyCart,
        }
        this.state = {
            categories: {},
            products: {},
            cart: {},
            cartCount: 0,
        }
    }

    render() {
        return (
            <AppContext.Provider value={{...this.state, ...this.actions}}>
                <App />
            </AppContext.Provider>
        )
    }

    emptyCart = p => {
        this.setState(state => produce(state, draft => {
            draft.cart = {}
            draft.cartCount = 0


        }))
    }

    getCartTotal = param => {
        const Prod_AR = Object.values(this.state.products)
        let icart = this.state.cart
        let aCart = Object.keys(icart)   

        //change keys from string to int
        let aiCart = []
        for (let i in aCart) {
            let iID = parseInt(aCart[i])
            aiCart.push(iID)
        }

        //find the product object and put it in a dictionary
        let aProds = {}
        for (let k in aiCart) {
            let obj = Prod_AR.find(p => p.id === aiCart[k])
            aProds[aiCart[k]] = obj
        }

        //get the values of the dictionary
        let oProds = Object.values(aProds)

        let tot = 0

        //total price
        for (let s in oProds) {
            tot += parseInt(oProds[s].price)*icart[oProds[s].id]
        }

        return tot
    }            

    addToCart = pid => {
        this.setState(state => produce(state, draft => {

            let id = pid.toString()
            let c = state.cart
            let a = Object.entries(c)
            const obj = a.find(o => o[0] === id)  

            //if obj is not undefined add 1 to existing quantity
            if (obj) {
                draft.cart[obj[0]] = 1 + obj[1]
            }

            //if obj is undefined create a new obj and add 1 to quantity
            else {
                draft.cart[pid] = 1
                draft.cartCount+=1
            }
        }))

    }

    removeFromCart = pid => {
        this.setState(state => produce(state, draft => {
            
            let id = pid.toString()
            let c = state.cart
            let a = Object.entries(c)
            const obj = a.find(o => o[0] === id)
                
            //delete object from array
            const index = a.indexOf(obj);
            if (index > -1) {
                a.splice(index, 1);
            }

            //create a dictionary with the remaining elements to match format
            const oCart = {}
            a.forEach(p => {
                oCart[p[0]] = p[1]
            })

            //set draft.cart to updated cart
            draft.cart = oCart

            //update cartCount
            let cnt = 0
            for (let p in oCart) {cnt+=1}
            draft.cartCount = cnt
        }))
    }

    async componentDidMount() {
        const resp = await axios.get('/api/category/')
        const prod = await axios.get('/api/product/')

        const cats = {}  //data type is dictionary
        for (const c of resp.data) {
            cats[c.id] = c
        }

        const prods = {}  //data type is dictionary
        for (const c of prod.data) {
            prods[c.id] = c
        }

        this.setState({
            categories: cats, //data type is dic
            products: prods,

        })
    }

}
