import React from 'react'
import {Navbar, Nav, NavDropdown, Image} from 'react-bootstrap'
import {Link} from "react-router-dom"
import AppContext from './context'

export default function HeaderContainer(props) {
    const context = React.useContext(AppContext)

    return (
        <Navbar bg="light" expand="lg">
            <Link to="/">            
                <Navbar.Brand><Image style={{width: '2.5rem'}} alt="Icon" src={`${process.env.PUBLIC_URL}/gallery/favicon.png`} width="3%"/> Arctic</Navbar.Brand>
            </Link>  
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="mr-auto">
                    <Link to="/home" className="nav-link">Home</Link>
                    <Link to="/about" className="nav-link">About</Link>
                    <Link to="/help" className="nav-link">Help</Link>            
                </Nav>
                <Nav>
                    <Link to='/cart'><i className="fas fa-shopping-cart"> ({context.cartCount})</i></Link>
                </Nav>
                <Nav>
                    <NavDropdown title="Hello, Joe" id="basic-nav-dropdown">
                        <NavDropdown.Item>Account</NavDropdown.Item>
                        <NavDropdown.Divider />
                    <NavDropdown.Item to="#action/3.4">Logout</NavDropdown.Item>
                    </NavDropdown>
                </Nav>
            </Navbar.Collapse>
        </Navbar>
    )
}

