import React from 'react'
import * as bs from 'react-bootstrap'
import { Formik, Form, Field} from 'formik'
import AppContext from './context'
import axios from 'axios'
import { Elements, CardElement, useStripe, useElements } from '@stripe/react-stripe-js';
import { loadStripe } from '@stripe/stripe-js';
import { useHistory } from 'react-router-dom'


const stripePromise = loadStripe("pk_test_zNK3XhObYFwq5x14hOffMBmw00vrDHfdmo")


function Checkout(props) {
    return (
        <Elements stripe={stripePromise}>
            <CheckoutController />
        </Elements>
    )
}
export default Checkout

const CheckoutController = props => {
    const context = React.useContext(AppContext)
    const total = context.getCartTotal()
    const stripe = useStripe();
    const elements = useElements();
    
    let history = useHistory()

    function handleClick() {
        history.push("/receipt") 
    }

    return (
        <Formik
            initialValues={{
                name: 'Gui de Souza',
                address1: '1234',
                address2: '5678',
                city: 'American Fork',
                state: 'UT',
                zipcode: '84003',
                total: total,
                items: context.cart,
                payment_intent: {}


            }}
            validateOnChange={false}
            validateOnBlur={false}
            validate={values => {
                const errors = {}
                console.log('validating', values)
                if (!values.name) {errors.name = 'Please type your name'}
                if (!values.address1) {errors.address1 ='Please type your address'}
                if (!values.city) {errors.city ='Please type your city'}
                if (!values.state) {errors.state ='Please type your state'}
                if (!values.zipcode) {errors.zipcode ='Please type your ZIP Code'}
                
                return errors
            }}
            onSubmit={async (values, actions) => {
                document.getElementById("btn").style.visibility = 'visible'
                console.log('submit', values)
                const resp = await axios.post('/api/sale/', {
                    "name": values.name,
                    "address1": values.address1,
                    "address2": values.address2,
                    "city": values.city,
                    "state": values.state,
                    "zipcode": values.zipcode,
                    "total": values.total,
                    "items": context.cart,
                    "payment_intent": {}                    
                })
                console.log(resp.data.client_secret)

                const result = await stripe.confirmCardPayment(resp.data.client_secret, {
                    payment_method: {
                      card: elements.getElement(CardElement),
                      billing_details: {
                        name: values.name,
                      },
                    }
                });

                if (result.error) {
                    // Show error to your customer (e.g., insufficient funds)
                    alert(result.error.message)
                } 
                
                else {
                    // The payment has been processed!
                    if (result.paymentIntent.status === 'succeeded') {
                        // Show a success message to your customer
                        handleClick()
                        context.emptyCart()
                    }                
                }

                document.getElementById("btn").style.visibility = 'hidden'
            }} 
        >{form => (
            <PaymentForm form={form} total={total} />
        )}</Formik>
    )
}

const PaymentForm = props => (
    
    <>
    <center className="mt-5"><h1>Checkout</h1></center>
    <Form>
        <bs.Row className='m-5'>
            <bs.Col md='5' >     
                <h2 style={{marginBottom: "2rem"}}>Shipping</h2>           
                <Input title="Name:" name="name" type="text" />
                <Input title="Address 1:" name="address1" type="text" />
                <Input title="Address 2:" name="address2" type="text" />
                <Input title="City:" name="city" type="text" />
                <Input title="State:" name="state" type="text" />
                <Input title="ZIP Code:" name="zipcode" type="text" />                
            </bs.Col>
            <bs.Col md='7'>
                <div style={{margin: '2rem', marginBottom: '2rem', marginTop: "0"}}>
                    <h2 className="mb-5">Payment</h2>
                    <div style={{border: "1px solid #000000", padding: "1rem", marginLeft: "0rem"}}>
                        <CardElement />      
                    </div>
                </div>        
                <div style={{margin: '2rem', marginBottom: '2rem', marginTop: "1rem"}}>
                    <p>Your total is: <b>${props.total}.00</b></p>
                </div>
                <div style={{marginLeft: "2rem"}}>
                    <bs.Button type="submit" disabled={props.form.isSubmitting}>Purchase</bs.Button>
                    <bs.Spinner id="btn" style={{visibility: "hidden", marginLeft: "1rem"}} size="sm" animation="border" role="status"></bs.Spinner>
                </div>                
            </bs.Col>
        </bs.Row>
    </Form>
    </>
)

const Input = (props) => (
    <Field name={props.name}>{rProps => (
        <bs.Form.Group>
            {props.title &&
                <bs.Form.Label>{props.title}</bs.Form.Label>
            }
            <bs.Form.Control
                type={props.type}
                placeholder={props.placeholder}
                disabled={rProps.form.isSubmitting}
                {...rProps.field}
            />
            {rProps.meta.touched && rProps.meta.error &&
                <div className="text-danger">{rProps.meta.error}</div>
            }
        </bs.Form.Group>
    )}</Field>
)