import React from 'react'
import {Container, Row, Col, Image, Button} from 'react-bootstrap'
import AppContext from './context'

export default function CartRow(props) {
    const price = parseInt(props.p.price).toFixed(2)
    const e = price * props.q
    const total = e.toFixed(2)
    const context = React.useContext(AppContext)
    return (
        <Container>
            <Row noGutters className="ml-2">
                <Col md="3">
                    <Container>
                        <Image className="m-1" style={{width: '6rem'}} src={`${process.env.PUBLIC_URL}/gallery/products/${props.p.filename}-1.png`}></Image>
                    </Container>
                </Col>
                <Col md="2">
                    <Container>
                        <p style={{fontSize: "1.2rem", marginTop: "2rem"}}>{props.q}</p>
                    </Container>
                </Col>
                <Col md="2">
                    <Container>
                        <p style={{fontSize: "1.2rem", marginTop: "2rem"}}>{props.p.price}</p>
                    </Container>
                </Col>
                <Col md="2">
                    <Container>
                        <p style={{fontSize: "1.2rem", marginTop: "2rem"}}>{total}</p>
                    </Container>
                </Col>
                <Col md="3">
                    <Container>
                        <Button onClick={e => {context.removeFromCart(props.p.id)}} className="mt-4">Remove</Button>
                    </Container>
                </Col>
            </Row>
        </Container>
    )
}