import React from 'react'
import {Container, Row} from 'react-bootstrap'
import './App.css';
import ProductCardCat from './product-card'
//import PRODUCTS from './products'
import AppContext from './context'

export default function Home(props) { 
    const context = React.useContext(AppContext)
    const Prod_AR = Object.values(context.products)
    
    return (
        <Container key={Prod_AR.id} fluid className="p-0">
            <Row noGutters style={{ padding: "1rem 0"}}>
                <ProductCardCat/>
            </Row>
        </Container>
    )
}