import React from 'react';
import './App.css';
import { Container, Row, Col } from 'react-bootstrap'
import { BrowserRouter as Router, Switch, Route} from "react-router-dom";
import HeaderContainer from './header-container'
import LeftContainer from './left-container'
import RightContainer from './right-container'
import FooterContainer from './footer-container'
import About from './about'
import Help from './help'
import Home from './home'
import ProductCardCat from './product-card';
import DetailsPage from './details';
import Cart from './cart';
import CartRow from './cart-row'
import Checkout from './checkout'
import Receipt from './receipt'

function App (props) {
  return (
    <Router>
      <Container fluid className="p-0 min-vh-100">
        <Row noGutters>
          <Col>
            <HeaderContainer/>
          </Col>
        </Row>       
        <Row noGutters>
          <Col md="2" className="px-3 py-4 leftcol"> 
            <LeftContainer/>
          </Col>        
          <Col md="8" className="shadow centercol">
            <Switch>
              <Route path="/details/:catID">
                <DetailsPage/>
              </Route>
              <Route path="/checkout">
                <Checkout/>
              </Route>
              <Route path="/receipt">
                <Receipt/>
              </Route>
              <Route path="/about">
                <About/>
              </Route>
              <Route path="/help">
                <Help/>
              </Route>
              <Route path="/category/:catName">
                <ProductCardCat/>
              </Route>
              <Route path="/cart">
                <Cart/>
              </Route>
              <Route path="/cart-row">
                <CartRow/>
              </Route>
              <Route path="/">
                <Home/>
              </Route>
            </Switch>
          </Col>        
          <Col md="2" className="px-3 py-4 shadow rightcol">
            <RightContainer/>
          </Col>
        </Row>  
        <Row noGutters>
          <Col className="footercol">
            <FooterContainer/>
          </Col>
        </Row>    
      </Container>
    </Router>
  );
}

export default App;
