import React from 'react'
import {Card, Col} from 'react-bootstrap'
import {Link} from "react-router-dom"

export default function CardCtg(props) {
    return(
        <Col key={props.p.id} md="3" style={{padding: "1rem"}}>   
            <Card style={{ width: '12rem' }}>
            <Card.Img variant="top" src={`${process.env.PUBLIC_URL}/gallery/products/${props.p.filename}-1.png`} />
            <Card.Body>
                <Card.Title style={{textAlign:"center"}} >{props.p.name}</Card.Title>
                <Card.Text style={{textAlign:"center"}}>
                    ${props.p.price}    
                </Card.Text>
                <Link className="btn btn-secondary position-absolute" to={`/details/${props.p.id}`} style={{ right: "0", top:"0"}}>Details</Link>                        
            </Card.Body>
            </Card>
        </Col>
    )
}