import React from 'react'
import {Container, Row, Col} from 'react-bootstrap'

const dArray = [
    ["Rubber Duck", "rubber_duck"], 
    ["Bag", "bag"],
    ["Bow", "bow"],
    ["Bread", "bread"],
    ["Bracelet", "bracelet"], 
    ["Camera", "camera"], 
    ["Drawer", "drawer"], 
    ["Soda Can", "soda_can"]]

export default function RightContainer(props) {
    return (
        <Container>
            <Row>
                <Col>
                    <div>
                        <h3>Recent Products:</h3><br></br>
                        {dArray.map((p, index) => {
                            return (
                                <div key={index}>
                                    <img alt={dArray[index][0]} className="center" style={{width: "50%"}} src={`${process.env.PUBLIC_URL}/gallery/products/${dArray[index][1]}-1.png`}></img>
                                    <div className="center" style={{fontSize: "1rem"}}>{dArray[index][0]}</div><br></br>
                                </div>
                            )
                        })}
                    </div>
                </Col>
            </Row>
        </Container>
    )
}