import React from 'react'
import {Nav} from 'react-bootstrap'
import {Link} from "react-router-dom";
import AppContext from './context'

export default function LeftContainer(props) {


    const context = React.useContext(AppContext)
    const categories = {}
    const Prod_AR = Object.values(context.products)
    
    for (let p of Prod_AR) {
        categories[p.category.title] = (categories[p.category.title] || 0) + 1
    }
    

    return (
        <>
            <Link to={`/`} className="nav-link">All Products - ({Prod_AR.length})</Link>
            <Nav defaultActiveKey="/home" link="black" className="flex-column text-dark">
                {Object.entries(categories).map(([key, value]) => (      
                    <Link key={key} to={`/category/${key}`} className="nav-link">{key} - ({value})</Link>
                    )                                            
                
                )}
            </Nav>
        </>
    )
}